import React, { useState } from "react";
import { useDispatch } from "react-redux";

import { NavLink } from "react-router-dom";
import { Container } from "components/GlobalStyle";
import { AuthContainer, BackroundAuth, ContainerText, FormUl, LabelForm, RegisterButton, RegisterForm, RegisterH1, RegisterH2, RegisterIMG, RegisterInput, RegisterLi, RegisterP, RegisterUL } from "./StyledAuth/Register.styled";
import { register } from "../../redux/auth/operation";
 import LogoRegister from './img/Vector.svg'

export const Register = () => {
   const dispatch = useDispatch();
   
  

   const handleSubmit = (e) => {
      e.preventDefault();
      const form = e.currentTarget;
      dispatch(register({
         name: form.name.value, 
         email: form.email.value,
         password: form.password.value
      }))
         .then(() => {
            console.log("Data submitted successfully!");
            // :  -      
         })
         .catch((error) => {
            console.error("Error submitting data:", error);
            // :  
         });
    
      
   };

   return (
      <section>
         <Container>
            <BackroundAuth>
               <AuthContainer>
               <RegisterForm onSubmit={handleSubmit}>
   <LabelForm htmlFor="username">
       <span>*</span>
      <RegisterInput
         placeholder="Username"
         id="username"
         type="text"
      />
   </LabelForm>
   <LabelForm>
         <span>*</span>
      <RegisterInput
         id="email"
         placeholder="E-mail"
         type="email"
      />
   </LabelForm>
   <LabelForm>
        <span>*</span>
      <RegisterInput
         id="password"
         placeholder="Password"
         type="password"
      />
   </LabelForm>
   <LabelForm>
         <span>*</span>
      <RegisterInput
         id="confirmPassword"
         placeholder="Confirm Password"
         type="password"
      />
   </LabelForm>
   <RegisterButton type="submit"></RegisterButton>
</RegisterForm>
<FormUl>
   <li>  <p> ?</p> </li>
   <li> <NavLink to='/login'></NavLink></li>
</FormUl>
    
     
               </AuthContainer>
          
            </BackroundAuth>
            <ContainerText>
            <RegisterH1>Books Reading</RegisterH1>

           

        <RegisterUL>
         <li>
         <RegisterH2> </RegisterH2>
         </li>
         <RegisterLi>
         <RegisterIMG src={LogoRegister} alt=''/>
            <RegisterP>
                  
            </RegisterP>
            </RegisterLi>
         <RegisterLi>
         <RegisterIMG src={LogoRegister} alt=''/>
            <RegisterP>
                 
            </RegisterP>
            </RegisterLi>
         <RegisterLi>
         <RegisterIMG src={LogoRegister} alt=''/>
            <RegisterP>
              
            </RegisterP>
            </RegisterLi>
        </RegisterUL>
  
  <RegisterUL>
   <li><RegisterH2>
      
   </RegisterH2></li>
   <RegisterLi>
      <RegisterIMG src={LogoRegister} alt=''/>
      <RegisterP>       </RegisterP>
   </RegisterLi>
   <RegisterLi>
   <RegisterIMG src={LogoRegister} alt=''/>
      <RegisterP>       </RegisterP>
   </RegisterLi>
   <RegisterLi>
   <RegisterIMG src={LogoRegister} alt=''/>
      <RegisterP>  </RegisterP>
   </RegisterLi>  </RegisterUL>
            </ContainerText>
        
      
         </Container>

      </section>
      
   );
};